import React, { useState, useEffect } from 'react'
import { animate, easeInOut } from 'popmotion'
import _ from 'lodash'
import Confetti from 'react-confetti'
import styled from 'styled-components'
import useWindowSize from '../hooks/useWindowSize'
import shuffle from '../helper/shuffle'
import ConfigPanel from './ConfigPanel'
import logo from './logo.svg'
import IconStar from './icon-star.svg'
import IconAMPOS from './icon-ampos.svg'
import IconAMPOScolor from './icon-ampos-color.svg'
import IconFSExit from './IconFullscreenExit.svg'
import IconFSEnter from './IconFullscreenEnter.svg'
import IconP from './icon-P.svg'
import IconPlay from './icon-PlayButton.svg'
import IconNextRound from './icon-NextRoundButton.svg'
import IconSoundOff from './IconSoundOff.svg'
import IconSoundOn from './IconSoundOn.svg'
import WinnerBackground from './prize-background.svg'
import SoundJingle from './jingle.mp3'
import SoundLotto from './sfx-lotto.mp3'

const Header = styled.header`
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
`
const SoundToggle = styled.button`
  outline: none;
  border: none;
  background: none;
  padding: 0;
  margin-right: 20px;
`
const FullscreenToggle = styled.button`
  outline: none;
  border: none;
  background: none;
  padding: 0;
`
const Footer = styled.footer`
  padding: 20px;
  display: flex;
  justify-content: end;
`
const Lottery = styled.div`
  position: relative;
  z-index: 10;
  .lottery {
    display: flex;
    justify-content: center;
    .list {
      display: flex;
      width: 590px;
      height: 590px;
      justify-content: center;
      align-items: center;
      margin: 0 -70px;
      border: 10px solid transparent;
      &.Prize {
        border-radius: 50%;
        border-color: white;
        // &.completed {
        //   border-color: transparent;
        //   background: url(${WinnerBackground}) no-repeat center;
        // }
      }
    }
  }
`
const LotteryResult = styled.div`
  width: 100%;
  height: 100%;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 11;
  &::before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    background: rgba(0,0,0,.4);
    display: block;
    z-index: -1;
  }
  .WinnerContainer {
    width: 400px;
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    .Winner, .WinnerBackground {
      width: 400px;
      height: 400px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: white;
      border-radius: 50%;
    }
    .WinnerAnnouncement {
      color: white;
      font-size: 36px;
      font-weight: bold;
      letter-spacing: 1px;
      text-transform: uppercase;
      position: absolute;
      margin-bottom: 600px;
    }
    .NewRoundBtn {
      position: absolute;
      margin-top: 600px;
    }
    .WinnerBackground {
      content: '';
      width: 600px;
      height: 600px;
      background: url(${WinnerBackground}) no-repeat center;
      background-size: 600px 600px;
      position: absolute;
    }
  }
`
const ConfigButton = styled.button`
  outline: none;
  border: none;
  display: flex;
  cursor: pointer;
  background: none;
  color: white;
  font: 16px 'Sora';
  padding: 0;
  margin-right: 20px;
  align-items: center;
  text-transform: uppercase;
`
const PlayButton = styled.button`
  background: #F96C2D;
  padding: 12px 16px;
  outline: none;
  border: none;
  border-radius: 8px;
  display: flex;
  cursor: pointer;
`
const OptionsPanel = styled.div`
  display: flex;
  flex-direction: row;
`
const InventoryToastr = styled.div`
  position: fixed;
  top: 20px;
  right: 20px;
  background: white;
  border-radius: 8px;
  z-index: -1;
  opacity: 0;
  transition: .3s;
  ul {
    padding: 10px;
    margin: 0;
    li {
      color: #333;
      list-style: none;
    }
  }
  .highlight {
    padding: 10px;
    background: #48B2B220;
    &::before {
      content: 'i';
      font: 14px bold 'Sora', sans-serif;
      line-height: 14px;
      display: inline-flex;
      margin-right: 10px;
      justify-content: center;
      align-items: center;
      width: 20px;
      height: 20px;
      border: 20px;
      border-radius: 20px;
      border: 2px solid black;
      color: black;
    }
    .dismiss {
      padding-left: 10px;
      font-size: 14px;
      color: #666;
      cursor: pointer;
    }
  }
  &.show {
    opacity: 1;
    z-index: 11;
  }
`
const EmptyLotteryArea = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  span {
    font: 16px bold 'Sora', sans-serif;
    margin: 0 10px;
    letter-spacing: 1px;
    text-transform: uppercase;
    border: 2px solid white;
    padding: 10px;
    border-radius: 8px;
    cursor: pointer;
  }
`
const EmptyLotteryState = ({ToggleConfigPanel, min}) => <EmptyLotteryArea>At least {min} items required to run the lottery, try <span onClick={() => ToggleConfigPanel()}>Inventory Config</span> </EmptyLotteryArea>


const App = () => {
  const DefaultItems = [
    {
      image: IconStar,
      name: 'Star',
      stock: 0
    },
    {
      image: IconAMPOS,
      name: 'AMPOS',
      stock: 0
    },
    {
      image: IconP,
      name: 'People',
      stock: 0
    }
  ]
  const [ShowConfigPanel, SetShowConfigPanel] = useState(false)
  const [InputItem, SetInputItem] = useState(DefaultItems)
  const [CurrentWinner, SetCurrentWinner] = useState(null)
  const [ActiveItem, SetActiveItem] = useState(-1)
  const [LotteryState, SetLotteryState] = useState('idle')
  const [SoundOn, SetSoundOn] = useState(false)
  const { width, height } = useWindowSize()
  const [InventoryToastrShow, SetInventoryToastrShow] = useState(false)
  const [FullscreenMode, SetFullscreenMode] = useState(false)

  const ConfigPanelParams = {
    closePanel: () => {
      SetShowConfigPanel(false)
    },
    InputItem: InputItem,
    SetInputItem: SetInputItem,
  }

  const ToggleConfigPanel = () => SetShowConfigPanel(!ShowConfigPanel)
  const AudioJingle = React.createRef()
  const AudioLotto = React.createRef()

  const ToggleJingle = () => {
    console.log(AudioJingle.current)
    if (SoundOn) {
      AudioJingle.current.play()
    } else {
      AudioJingle.current.pause()
      AudioJingle.current.currentTime = 0
    }
    SetSoundOn(!SoundOn)
  }

  const LotteryConfig = {
    duration: 300,
    speed: 400,
    confettiTime: 5000
  }
  const ResetRound = () => {
    SetActiveItem(-1)
    SetLotteryState('idle')
  }

  const ToggleFullScreen = () => {
    if (!document.fullscreenElement) {
      document.documentElement.requestFullscreen()
      SetFullscreenMode(!FullscreenMode)
    } else {
      if (document.exitFullscreen) {
        document.exitFullscreen()
      }
    }
  }
  const Prize = () => {
    //Calculate Chance
    let items = []
    InputItem.map((elem, index) => {
      for (let i=0; i<parseInt(elem.stock); i++) items.push(index)
    })
    console.log('items before shuffle: '+items)
    items = shuffle(items)
    console.log('items after shuffle: '+items)
    const winner = items[Math.floor(Math.random()*items.length)]
    console.log('Winner: '+winner)
    return winner
  }
  const DisplayWinnerAndRemoveItemFromInventory = () => {
    const WinnerIndex = Prize()
    SetCurrentWinner(WinnerIndex)
    SetActiveItem(WinnerIndex)
    SetLotteryState('completed')

    const NewStock = InputItem.map((obj, objIndex) => {
      if (objIndex === WinnerIndex) {
        return {...obj, stock: obj.stock - 1}
      }
      return obj
    })
    console.log({NewStock})
    SetInputItem(NewStock)

  }
  const LotteryStart = () => {
    AudioLotto.current.play()
    animate({
      from: 0,
      to: InputItem.length,
      repeat: 11,
      duration: LotteryConfig.duration,
      ease: easeInOut,
      onUpdate: latest => SetActiveItem(Math.floor(latest)),
      onComplete: () => DisplayWinnerAndRemoveItemFromInventory()
    })
  }

  window.preloadedPictures = [WinnerBackground]

  const InventoryToasterDismissing = () => SetInventoryToastrShow(false)
  const MinStockItems = 2
  const CheckEnoughItemToPlayLottery = (item, minimum) => {
    //Check number of item to have at least MinStockItems in order to play lottery
    let InStockItem = 0
    item.map(elem => parseInt(elem.stock) > 0 && (InStockItem++))
    console.log(parseInt(InStockItem))
    return (InStockItem >= minimum) ? true : false
  }

  useEffect(() => {
    SetInventoryToastrShow(true)
    setTimeout(() => SetInventoryToastrShow(false), 5000)
  }, [InputItem])

  return (
    <div className='App'>
      <Header>
        <img src={logo} alt='AMPOS' />
        <OptionsPanel>
          <audio ref={AudioJingle} preload='true' volume={0.5} src={SoundJingle} loop />
          <ConfigButton onClick={()=>ToggleConfigPanel()}>Config</ConfigButton>
          <SoundToggle onClick={()=>ToggleJingle()}>{SoundOn ? <img src={IconSoundOff} /> : <img src={IconSoundOn} />}</SoundToggle>
          <FullscreenToggle onClick={() => ToggleFullScreen()}>{FullscreenMode ? <img src={IconFSExit} alt='Exit Full Screen' /> : <img src={IconFSEnter} alt='Enter Full Screen' /> }</FullscreenToggle>
        </OptionsPanel>
      </Header>
      {CheckEnoughItemToPlayLottery(InputItem, MinStockItems) ? <Lottery>
        <div className='lottery'>
          {InputItem.map((elem, index)=><div key={'items-'+index} className={(ActiveItem===index?`list Prize ${LotteryState}`:'list')}><img src={elem.image} /></div>)}
        </div>
      </Lottery> : <EmptyLotteryState ToggleConfigPanel={ToggleConfigPanel} min={MinStockItems} />}
      <Footer>
        {(LotteryState==='completed')?<PlayButton onClick={()=>ResetRound()}><img src={IconNextRound} alt='Next Round' height='21' /></PlayButton>:
        <PlayButton onClick={()=>LotteryStart()}><img src={IconPlay} alt='Play' height='21' /></PlayButton>}
      </Footer>
      {LotteryState==='completed'&&<Confetti numberOfPieces={1000} opacity={.8} tweenDuration={LotteryConfig.confettiTime} width={width} height={height} />}
      <audio ref={AudioLotto} src={SoundLotto} preload='true' autoPlay={false} />

      {(LotteryState==='completed')&&<LotteryResult>
        <div className='WinnerContainer'>
          <div className='WinnerBackground'></div>
          <h3 className='WinnerAnnouncement'>Winner</h3>
          <div className='Winner'>
            <img src={InputItem[CurrentWinner].name === 'AMPOS' ? IconAMPOScolor : InputItem[CurrentWinner].image} alt={InputItem[CurrentWinner].name} />
          </div>
          <PlayButton className='NewRoundBtn' onClick={()=>ResetRound()}><img src={IconNextRound} alt='Next Round' height='21' /></PlayButton>
        </div>
      </LotteryResult>}

      {ShowConfigPanel && <ConfigPanel options={ConfigPanelParams} />}
      <InventoryToastr className={InventoryToastrShow && 'show'}>
        <div className='highlight'>Inventory Updated! <span className='dismiss' onClick={()=>InventoryToasterDismissing()}>Dismiss</span></div>
        <ul>
          {InputItem.map(elem => <li key={elem.name}>{elem.name}: <strong>{elem.stock}</strong></li>)}
{/*          <li>Star: <strong>{_.get(_.find(InputItem, {name: 'Star'}), 'stock')}</strong></li>
          <li>AMPOS: <strong>{_.get(_.find(InputItem, {name: 'AMPOS'}), 'stock')}</strong></li>
          <li>P: <strong>{_.get(_.find(InputItem, {name: 'People'}), 'stock')}</strong></li>
*/}        </ul>
      </InventoryToastr>
    </div>
  )
}

export default App
