import React, {useState, useEffect} from 'react'
import styled from 'styled-components'
import _ from 'lodash'

import useWindowSize from '../hooks/useWindowSize'
import TarotObject from './tarot.json'
import TarotCard from './tarot-back.jpg'

const TarotWrapper = styled.div`
	width: 100%;
	height: auto;
	min-height: 100vh;
	background: #eee;
	display: flex;
	justify-content: center;
	align-items: center;
	.TarotHeader {

	}
	.TarotHeader, .TarotFooter {
		margin: 2rem 0;
	}
	.TarotFooter {
		.btn-primary {
			background: linear-gradient(94.49deg, #48B2B2 0.8%, #557BC7 83.11%);
			border: 0;
		}
	}
	.TarotContainer {
		height: auto;
		position: relative;
		.TarotContent {
			position: relative;
			width: 100%;
			&::after {
				content: '';
				display: block;
				position: relative;
				padding-bottom: 100%;
				z-index: -1;
			}
			.TarotCard {
				cursor: pointer;
				position: absolute;
				transform-origin: center;
				transition: .3s;
				&:hover,
				&.selected {
					z-index: 100!important;
				}
				&.selected {
					box-shadow: 0 0 10px #0076ff, 0 0 100px #fff;
				}
				img {
					width: 70px;
					box-shadow: 0 0 5px #032a5733;
				}
				&.shuffle {
					transform: translate(50%,50%)!important;
				}
			}
		}
	}
`
const TarotReadProphecy = styled.div`
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 101;
	display: flex;
	justify-content: center;
	align-items: center;
	.ProphecyContent {
		background: white;
		padding: 1rem;
		position: relative;
		z-index: 1;
		max-width: 360px;
		box-shadow: 0 0 30px #032a5799;
		.ProphecyTitle {
			text-align: center;
			font: 24px 'Sora', 'Noto Sans Thai';
		}
		.ProphecyCard {
			img {
				max-width: 100%;
			}
		}
		.ProphecyMeaning {
			text-align: center;
			padding: 1rem 0 0;
			color: #333;
		}
		.ProphecyDisclaimer {
			font-size: 11px;
			color: #999;
		}
	}
	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: #032a5733;
	}
`
const InfoBox = styled.div`
	--border-size: 4px;
  position: relative;
  padding: var(--border-size);
  box-sizing: border-box;
  background-clip: padding-box;
  border: solid var(--border-size) transparent;
  border-radius: 8px;
  margin: 10px 0;
	&::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: calc(0 - var(--border-size)); /* !important */
    border-radius: inherit; /* !important */
    background: linear-gradient(94.49deg, #48B2B2 0.8%, #557BC7 83.11%);
	}
	.content {
	  border-radius: 4px;
	  padding: .5rem;
  	background: #eee;
  	position: relative;
  	z-index: 1;
	}
`
const Tarot = () => {
	const [TarotItems, SetTarotItems] = useState([])
	const [SelectedTarotCard, SetSelectedTarotCard] = useState(null)
	const [TarotContentSize, SetTarotContentSize] = useState(0)
	const [IsShuffleTarotCard, SetIsShuffleTarotCard] = useState(false)
	const [ShowPickTarotCardFirst, SetShowPickTarotCardFirst] = useState(false)
	const [IsReadProphecy, SetIsReadProphecy] = useState(false)
	const [IsShowDisclaimer, SetIsShowDisclaimer] = useState(false)

  const { width, height } = useWindowSize()

  //Prep Tarot Card
  const ShuffleTarotItems = () => {
  	const TarotItems = []
		_.shuffle(TarotObject).map((element, index) => {
			TarotItems[index] = element
			TarotItems[index].image = require(`./tarot-${element.id}.jpg`)
		})
		SetTarotItems(TarotItems)
  }

	const SelectTarotCard = (id) => {
		SetSelectedTarotCard(id)
		SetShowPickTarotCardFirst(false)
	}
	const TarotHeight = 120
	const TarotWidth = 70
	const TarotContentRef = React.createRef()
	const CircularPosition = (index, total, ContainerSize, ObjectHeight) => {
		let PositionTop = 0
		let PositionLeft = 0
		
		//Drawing position
		console.log(ContainerSize, ObjectHeight)
		const theta = 1.75 * Math.PI * (index / total)
		const radius = (ContainerSize-(ObjectHeight))/2
		const HorizontalCenter = radius+(ObjectHeight/4)
		const VerticalCenter = radius

		PositionTop = HorizontalCenter + radius * Math.sin(theta)
		PositionLeft = VerticalCenter - radius * Math.cos(theta)

		return {
			transform: `translate(${PositionTop}px, ${PositionLeft}px) rotate(${index*315/total}deg)`,
			zIndex: (total - index)
		}
	}

	const ShuffleTarotCard = () => {
		ShuffleTarotItems()
		SetIsShuffleTarotCard(true)
		setTimeout(() => {
			SetIsShuffleTarotCard(false)
		}, 1000)
	}
	const ReadTarot = () => {
		if (SelectedTarotCard === null) {
			HighlightNotPickedCard()
			return false
		}
		SetIsReadProphecy(true)
	}
	const HighlightNotPickedCard = () => {
		SetShowPickTarotCardFirst(true)
		setTimeout(() => {
			SetShowPickTarotCardFirst(false)
		}, 5000)
	}
	const OpenDisclaimer = () => {
		SetIsShowDisclaimer(!IsShowDisclaimer)
	}
	useEffect(() => {
		document.title = 'AMPOS Tarot'
		ShuffleTarotItems()
	}, [])
	useEffect(() => {
		SetTarotContentSize(_.get(TarotContentRef, `current.offsetWidth`))
	}, [width])
	useEffect(() => {
		console.log(_.find(TarotItems, element => element.id === 1))
	}, [TarotItems])
	return (
		<TarotWrapper>
			<div className='container'>
				<div className='TarotHeader row justify-content-center'>
				<h2>Daily Tarot</h2>
				<p>Pick a card to read the prophecy.</p>
				</div>
				<div className='TarotContainer'>
					<div className='TarotContent' ref={TarotContentRef}>
						{TarotObject.map((element, index, object) => <div className={(SelectedTarotCard===index ? 'TarotCard selected' : 'TarotCard') + (IsShuffleTarotCard ? ' shuffle' : '')} key={`card-${element.id}`} style={CircularPosition(index, object.length, TarotContentSize, TarotHeight)} onClick={()=>SelectTarotCard(element.id)}><img src={TarotCard} alt={`Tarot Card`} /></div>)}
					</div>
				</div>
				<div className='TarotFooter row justify-content-center'>
					<div className='col d-grid gap-2'>
						<button type='button' className='btn btn-light' onClick={()=>ShuffleTarotCard()}>Shuffle</button>
					</div>
					<div className='col d-grid gap-2'>
						<button type='button' className='btn btn-primary' onClick={() => ReadTarot()}>Read Tarot</button>
					</div>
					{ShowPickTarotCardFirst && <div className='row justify-content-center'>
						<div className='col'>
							<InfoBox><div className='content'>You need to select a card to read the prophecy.</div></InfoBox>
						</div>
					</div>}
				</div>
			</div>
			{IsReadProphecy && <TarotReadProphecy className={IsReadProphecy && 'show'}>
				<div className='ProphecyContent'>
					<div className='ProphecyTitle'>{_.get(_.find(TarotItems, element => element.id === SelectedTarotCard), 'name')}</div>
					<div className='ProphecyCard'><img src={_.get(_.find(TarotItems, element => element.id === SelectedTarotCard), 'image')} alt={_.get(_.find(TarotItems, element => element.id === SelectedTarotCard), 'name')} /></div>
					<div className='ProphecyMeaning'><p>{_.get(_.find(TarotItems, element => element.id === SelectedTarotCard), 'meaning')}</p><button className='btn btn-outline-secondary btn-sm' onClick={()=>OpenDisclaimer()}>Disclaimer</button></div>
					{IsShowDisclaimer && <div className='ProphecyDisclaimer'>  100%   //      </div>}
				</div>
			</TarotReadProphecy>}
		</TarotWrapper>
	)
}

export default Tarot